@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.page__body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  background-color: #ffffff;
  color: #444444;
}
.size-fix {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .size-fix {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .size-fix {
    width: 1440px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.page-header--main {
  position: relative;
}
@media (min-width: 1440px) {
  .page-header--main::before {
    content: "";
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    right: 0;
    background-color: #63a63b;
    z-index: -1;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 24px 30px 22px;
    box-sizing: border-box;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    width: 1440px;
    padding: 53px 110px 0;
  }
}
.page-header__mobile-wrapper {
  border-bottom: 1px solid #e6e6e6e6;
}
@media (min-width: 768px) {
  .page-header__mobile-wrapper {
    border: none;
  }
}
@media (min-width: 1440px) {
  .page-header__mobile-wrapper {
    position: relative;
    z-index: 10;
  }
}
.page-header__size-fix {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  min-height: 65px;
  margin: 0 auto;
  padding: 0 20px 6px;
  box-sizing: border-box;
  position: relative;
}
@media (min-width: 768px) {
  .page-header__size-fix {
    min-height: auto;
    margin: 0;
    padding: 0;
    width: auto;
  }
}
.page-header__logo {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-decoration: none;
}
.page-header__toggle {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin-left: auto;
}
.page-header__toggle:hover,
.page-header__toggle:focus {
  opacity: 0.8;
  outline: none;
}
.page-header__toggle--close::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  top: 13px;
  right: 0;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.page-header__toggle--opened::after,
.page-header__toggle--opened::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 1.5px;
  top: 19px;
  right: 0;
  background-color: #000000;
}
.page-header__toggle--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle--opened::before {
  box-shadow: none;
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle--nojs,
.page-header__toggle--nojs::before,
.page-header__toggle--nojs::after {
  display: none;
}
.logo:hover {
  opacity: 0.8;
}
.logo:focus {
  border: none;
  outline: none;
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
.logo__icon {
  display: block;
}
@media (min-width: 768px) {
  .logo__icon {
    width: 61px;
    height: auto;
    margin-right: 12px;
  }
}
@media (min-width: 1440px) {
  .logo__icon {
    width: 72px;
  }
}
.logo__text {
  position: absolute;
  left: 50%;
  margin-left: -50px;
  top: 50%;
  margin-top: -9px;
}
@media (min-width: 768px) {
  .logo__text {
    position: static;
    margin-left: 0;
    margin-top: 6px;
  }
}
@media (min-width: 1440px) {
  .logo__text {
    margin-top: 9px;
    height: 22px;
    width: 118px;
  }
}
.page-nav {
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-nav {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 18px;
  }
}
@media (min-width: 1440px) {
  .page-nav {
