.radio {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  cursor: pointer;
}
.radio:hover .radio__mark,
.radio:active .radio__mark {
  border: 1px solid #bababa;
}
.radio:focus-within .radio__mark {
  outline: #68b738;
}
.radio__descriprtion {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
}
@media (min-width: 768px) {
  .radio__descriprtion {
    font-size: 20px;
    line-height: 30px;
  }
}
.radio__mark {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  background-color: transparent;
  position: relative;
  flex-shrink: 0;
  border-radius: 50%;
  margin-right: 15px;
}
@media (min-width: 768px) {
  .radio__mark {
