    margin-right: 20px;
  }
}
.radio__input:checked + .radio__mark::before {
  content: "";
  position: absolute;
  top: 25%;
  left: 25%;
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .radio__input:checked + .radio__mark::before {
    top: 26%;
    left: 26%;
  }
}
@media (min-width: 768px) {
