    width: 18px;
    height: 18px;
  }
}
.radio__input:checked:focus + .radio__mark {
  border: 1px solid #bababa;
  outline: 1px solid #68b738;
}
.radio__input[disabled] ~ .radio__mark,
.radio__input[disabled] ~ .radio__descriprtion {
  cursor: auto;
  opacity: 0.3;
}
